export const userEmailDuplicateCheck = (body) => {
    return new Promise((resolve, reject) => {
        axios.post(`${USER_API}/dupCheck`, body)
            .then(response => {
                if (response.data.success) {
                    resolve(response.data.available)
                } else {
                    throw { success: false }
                }
            })
            .catch(err => {
                reject(err)
            })
    })
}
