import { message } from 'antd';
import axios from 'axios';
import React, { memo, useContext, useState } from 'react'
import { useSelector } from 'react-redux';
import {Modal,ModalHeader,ModalBody, ModalFooter, Button, Label, Input} from 'reactstrap'
import { CLOUD_API } from '../../../../../../route/Apis';
import { CloudBoardContext } from '../../../CloudViewer';


const FolderCreateModal=memo((props) =>{
    //  
    const {refreshFileList} = useContext(CloudBoardContext)
    const user = useSelector(state => state.user)
    const folderPath = useSelector(state => state.folder.path);
    const {isOpen,ModalHandler} = props;
    const [folderName, setFolderName] = useState("");
    const onCloseModal = () => {
        ModalHandler(false)
    };
    const onConfirmModal =()=>{
        console.log(folderPath);
        if(folderName===""){
            alert("  ");
            return false;
        }
        
        let body = {
            //File   
            filename : folderName,
            originalname : folderName,
            cloudpath : folderPath,
            writer : user.userData._id,
            mimetype : "Folder",
            importance : 1,
        };

        axios.post(`${CLOUD_API}/folder/create`,body)
        .then(response=>{
            if(response.data.success){
                message.success(" .");
                ModalHandler(false)
                refreshFileList();
            }else{
                alert(" . \n  ");
            }
        })
    }
    const onFolderNameHandler=(e)=>{
        setFolderName(e.currentTarget.value);
    }
    return (
        <>
        <Modal isOpen={isOpen} style={{minWidth:'600px'}}>
        <ModalHeader toggle={onCloseModal}> </ModalHeader>
        <ModalBody>
                    <Label ></Label>
                    <Input className="form-control"  placeholder="  " value={folderName} onChange={onFolderNameHandler} />
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={onConfirmModal}></Button>{' '}
          <Button color="secondary" onClick={onCloseModal}></Button>
        </ModalFooter>
      </Modal>
      </>
    )
})

export default FolderCreateModal
